.footer {
  padding-top: 5px;
  
  background-color: $cold-dark-color;
  margin: 0px;

  .footer__logo-rrss-container{
    border-bottom: solid 1px $inverted-text-color;
  .footer__logo-container{
      height: 60px;
      @include mobile-logo;


      .footer__logo-image-container{
          height: 50px;
          img {
              height: 50px;
          }
      }
      .footer__logo-title{
        color: $inverted-text-color;          
        font-size: 20px;
          
      }
  }

  .footer-rrss {
    display: flex;
    flex-direction: row;
    
  li {
  padding: 10px;
    img {
      height: 35px;}
  }
  }
  }

.footer__menu-list {
  @include mobile-text;
  border-bottom: solid 1px $inverted-text-color;
  color: $inverted-text-color;
  font-weight: 900;
  li {
    padding: 8px;
  }
}
.footer__workers-list{
  @include mobile-text;
  color: $inverted-text-color;
  font-weight: 900;
}
}


@media (min-width: 768px) {

  .footer {
    .footer__without-workers{
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      border-bottom: solid 1px $inverted-text-color;
      color: $inverted-text-color;
    .footer__logo-rrss-container{
      border-bottom: none;
      padding: 12px;
  
    .footer-rrss {

    li {

      img {
}
    }
    }
    }
  
  .footer__menu-list {
    @include tablet-text;
    border-bottom: none;
    color: $inverted-text-color;
    font-weight: 900;
    display: flex;
    flex-direction: row;
    min-width: 235px;
    
        li {
      padding: 8px;
    }
  }

}
  .footer__workers-list{
    @include mobile-text;
    color: $inverted-text-color;
    font-weight: 900;
  }
  }
  
  


}





@media (min-width: 1200px) {
  .footer{

    padding-left: 100px;
    padding-right: 100px;


}
}